
import React, { Component } from "react"
import { elements } from "./_data"

export default class Element extends Component {
  state = {
    hover: false
  }

  openInfo = event => {
    this.props.showInfo(this.props.num)
  }

  onMouseEnter = event => {
    this.setState({ hover: true })
  }

  onMouseLeave = event => {
    this.setState({ hover: false })
  }

  render() {
    let { num } = this.props
    let element = elements[num]
    if (element.borders) {
      console.log("BORDER:", element.borders)
    }
    return (
      <div
        title={element.name}
        onMouseEnter={this.onMouseEnter}
        onMouseLeave={this.onMouseLeave}
        onClick={this.openInfo}
        className={`tooltip element element-${num} ${element.category} ${
          this.state.hover ? "active" : ""
        } ${element.borders &&
          element.borders.length > 0 &&
          element.borders.map(item => `border-${item}`).join(" ")}`}
      >
        <div className="number">{element.number}</div>
        <div className="symbol">{element.symbol}</div>
        <div className="element-name">{element.name}</div>
        {/* <div className="tooltiptext">
          <p>Cu trc mng tinh th</p>
          <p>fcc: lp phng tm din</p>
          <p>bcc: lp phng tm khi</p>
          <p>hcp: lc gic b cht</p>
          <p>cube: lp phng</p>
          <p>hex: lc gic</p>
        </div> */}
      </div>
    )
  }
}
